body {
  font-family: 'Roboto', sans-serif; }

button {
  background: none;
  border: none; }

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden; }
  .card .card-header {
    background-color: #009FE3;
    color: #F8F8F8;
    margin-bottom: 10px;
    padding-left: 10px;
    padding-top: 20px;
    padding-bottom: 20px; }
    .card .card-header h3 {
      margin: 0;
      font-weight: normal; }
  .card .card-body {
    padding: 2px 16px; }
  .card .card-footer {
    margin-top: 10px;
    margin-bottom: 5px;
    padding-left: 10px;
    padding-top: 5px;
    border-top: 2px #D3D3D3 solid; }

.todoTaskContainer {
  padding: 5px 0;
  border-bottom: 1px #D3D3D3 solid; }
  .todoTaskContainer .taskLabel {
    margin-right: 1rem;
    cursor: pointer; }
  .todoTaskContainer i {
    cursor: pointer; }
  .todoTaskContainer .remove-btn {
    position: absolute;
    right: 0;
    margin-left: 5px;
    color: #D3D3D3;
    cursor: pointer;
    display: none; }
  .todoTaskContainer.finished-task {
    color: #D3D3D3;
    text-decoration: line-through; }
    .todoTaskContainer.finished-task i {
      color: #509901; }
  .todoTaskContainer.accent-color-hover {
    color: #509901; }
    .todoTaskContainer.accent-color-hover .remove-btn {
      color: #509901; }
    .todoTaskContainer.accent-color-hover i {
      color: #509901; }
  .todoTaskContainer.warn-color-hover {
    color: #F95454; }
    .todoTaskContainer.warn-color-hover .remove-btn {
      color: #F95454; }
    .todoTaskContainer.warn-color-hover i {
      color: #F95454; }
  .todoTaskContainer.general-hover .remove-btn {
    display: inline-block; }

#todoContainer {
  width: 50vw;
  min-width: 210px;
  margin: 50px auto 10px auto; }

#createTaskContainer {
  margin-top: 5px;
  margin-bottom: 5px; }
  #createTaskContainer button {
    color: #009FE3;
    cursor: pointer; }
  #createTaskContainer #taskInput {
    border: none;
    border-bottom: 2px #009FE3 solid; }
    #createTaskContainer #taskInput::placeholder {
      color: #009FE3; }

#taskListContainer {
  margin-top: 10px;
  margin-bottom: 10px; }

#filterContainer .selected-filter {
  color: #009FE3;
  text-decoration: underline; }

#filterContainer button {
  cursor: pointer; }
